@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,100&family=Roboto+Condensed:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&family=Raleway:ital,wght@1,100&family=Roboto+Condensed:ital,wght@1,300&display=swap);
*{
  box-sizing:border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}
.heading_style{
  padding: 30px 0;
  text-align: center;
  font-family: "Raleway",sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  /* color: #868686; */
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}
.cards{
  width: 100%;
  height: auto;
}
.card{
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
  background-color: #fff;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}
.card:hover{
  box-shadow: 0px 30px 18px -8px rgba(0,0,0,0.1);
  -webkit-transform:scale(1.05,1.05);
          transform:scale(1.05,1.05);
}
.card__img{
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  height: 235px;
  width: 100%;
}
.card__info{
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: #fff;
}
.card__category{
  font-family: "Raleway",sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
}
.card__title{
margin-top: 5px;
margin-bottom: 10px;
font-family: "Raleway",sans-serif;
background-color: #fff;
text-transform: capitalize;
}
button{
  padding: 5px 8px;
  font-family: "Raleway",sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  /* color: #868686; */
  background-color: #fff;
  outline: none;
  border: 1px soloid black;
  cursor: pointer;
}

#a,button:hover{
  color: blue;
}

.slot_inner{
  display: flex;
  justify-content: center;
}
.slot_outer{
  display: flex;
  justify-content: center;
}
.slotmachine{
box-sizing: border-box;
border-color: black;
height: auto;
width: auto;

}
